.annotation-table {
  padding: 16px 16px;
  margin-top: 50px;
  .text-gradient {
    margin-left: 10px;
    font-weight: bold;
    font-size: 32px;
    background: #7f7fd5; /* fallback for old browsers */
    background: -webkit-linear-gradient(right, #7f7fd5, #86a8e7, #91eae4);
    background: linear-gradient(to right, #7f7fd5, #86a8e7, #91eae4);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  button {
    margin-top: 20px;
  }
}
