@import url(https://fonts.googleapis.com/css?family=Major+Mono+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-title{font-size:9vw;text-align:center;color:#ee53a8;font-family:"Major Mono Display", monospace}.github-btn{color:#8c8c8c;border-color:#8c8c8c;position:absolute;top:10px;right:16px}.github-btn a{color:#8c8c8c;text-decoration:none}

.setting-area{padding:16px 16px}.setting-area .ant-form label{font-size:32px;font-weight:bold;text-align:center;background:#f12711;background:linear-gradient(to right, #f12711, #f5af19);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.setting-area .ant-form .ant-form-item-required::before{content:""}

.annotation-table{padding:16px 16px;margin-top:50px}.annotation-table .text-gradient{margin-left:10px;font-weight:bold;font-size:32px;background:#7f7fd5;background:linear-gradient(to right, #7f7fd5, #86a8e7, #91eae4);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.annotation-table button{margin-top:20px}

.highlight-area{padding:16px 16px}.highlight-area .result-title{margin-top:132px;font-weight:bold;text-align:center;background:#12c2e9;background:linear-gradient(to right, #12c2e9, #c471ed, #f64f59);-webkit-background-clip:text;-webkit-text-fill-color:transparent}


